@import url('https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,300;0,400;1,400&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Mulish', sans-serif;
}

.navbar{
    background-color: #fff !important;
}

.navbar-brand{
    font-size: 1.9rem !important;
    color: #565387;
}

.navbar a{
    font-size: 1.3rem;
    text-transform: capitalize;
}

.menu_active{
    font-weight: bold;
    border-bottom: 1px solid #565387;
}

.navbar a:hover{
   color: #3498db;
}

/*#Main heading*/

#header{
    width: 100%;
    height: 80vh;
    margin-top: 50px;

}

#header h1{
    margin: 0 0 5px 0;
    font-size: 30px;
    font-weight: 500;
    color: black;    
}

#header .brand_name{
    color: #3498db;
}

#header h2{

    font-size: 20px;
  
    color: #484848;    
}

#header .btn_get_started{
    font-weight: 250;
    font-size: 13px;
    letter-spacing: 1px;
    display: inline-block;
    padding: 10px 30px;
    border-radius: 50px;
    transition: 0.5s;
    color: #3498db;
    border: 2px solid #3498db;
    text-decoration: none;
}

#header .btn_get_started:hover{
    background: #3498db;
    color: #fff;
}

.header-img{
    text-align: center;
}

#header .animated {
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down {
    0% {
        transform: translateY(10px);
    }
    100%{
        transform: translateY(-10px);
    }
}

@keyframes up-down{
    0% {
        transform: translateY(10px);
    }
    100%{
        transform: translateY(-10px);
    }
}

/* Resposive code*/

@media (max-width:991px) {
    #header{
      
        height: 50%;
        text-align: center;
    
    }
    #header .header-img{
        text-align: center;
    }

    #header .header-img img{
        width: 60%;
    }
       
}


@media (max-width:786px) {
    #header{
      
        margin-top: 20px;
       
    }
    #header h1{
        font-size: 20px;
    }

    #header h2{
       font-size: 18px;
       margin-bottom: 30px;
    }

    #header .header-img img{
        width: 70%;
    }
       
}